const IndexPage: React.FC<PageProps<DataProps>> = ({ data, location }) => (
  <Layout>
    <div className={styles.textCenter}>
      <StaticImage
        src="../../images/example.png"
        loading="eager"
        width={64}
        quality={95}
        formats={["auto", "webp", "avif"]}
        alt=""
        style={{ marginBottom: `var(--space-3)` }}
      />
    </div>
    <ul className={styles.list}>
      {data.allMarkdownRemark.nodes.map(({ frontmatter: link }) => (
        <li key={link.slug} className={styles.listItem}>
          <Link className={styles.listItemLink} to={`/blog${link.slug}`}>
            {link.title} ↗
          </Link>
          <p className={styles.listItemDescription}>{link.description}</p>
        </li>
      ))}
    </ul>
  </Layout>
)

export const pageQuery = graphql`
  {
    allMarkdownRemark {
      nodes {
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          slug
          title
        }
      }
    }
  }
`

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head: React.FC<PageProps<DataProps>> = () => <Seo title="Blog" />

export default IndexPage
